html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  color: var(--vy-dk-blue);
  font-size: 1.125rem;
  font-weight: 400;
  background-color: var(--dk-cyan);
  display: grid;
  place-items: center;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.bg-top {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 100%;
  height: 100%;
  z-index: -1;
}
@media (min-width: 71.875em) {
  .bg-top {
    height: 150vh;
    left: -2%;
    top: -83%;
    width: 50%;
  }
}

.bg-bot {
  position: absolute;
  bottom: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  z-index: -1;
}
@media (min-width: 71.875em) {
  .bg-bot {
    bottom: -75%;
    height: 125vh;
    right: -11%;
    width: 60%;
  }
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dk-cyan: hsl(185, 75%, 39%);
  --vy-dk-blue: hsl(229, 23%, 23%);
  --dk-gr-blue: hsl(227, 10%, 46%);
  --dk-gray: hsl(0, 0%, 59%);
}

:root {
  --font-body: "Kumbh Sans", sans-serif;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 1.5625rem;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

.card {
  border-radius: 1.25rem;
  overflow: hidden;
  background-color: var(--white);
  text-align: center;
}
.card__banner {
  width: 20.4375rem;
  height: 8.75rem;
  background-image: url("/images/bg-pattern-card.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
}
@media (min-width: 40em) {
  .card__banner {
    width: 22.0625rem;
  }
}
.card__user {
  position: relative;
  border-bottom: 0.0625rem solid #e6e6e6;
  padding-top: 4.625rem;
  color: var(--dk-gr-blue);
}
.card__user img {
  position: absolute;
  width: 6.125rem;
  height: 6.125rem;
  border-radius: 50%;
  border: 0.3125rem solid var(--white);
  left: 50%;
  top: 0;
  transform: translate(-50%, -50%);
}
.card__user--name {
  font-weight: 700;
  color: var(--vy-dk-blue);
  margin-bottom: 0.625rem;
}
.card__user--age {
  padding-left: 0.75rem;
  font-weight: 400;
  color: var(--dk-gr-blue);
}
.card__user--loc {
  font-size: 0.75rem;
  margin-bottom: 1.4375rem;
}
.card__info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1.63125rem 2.6875rem;
}
.card__info a {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 0.625rem;
  font-weight: 400;
  color: var(--dk-gray);
}
.card__info a span {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--vy-dk-blue);
  margin-bottom: 0.5rem;
}